.token{
   background: url(../../assets/img/f5/token_bg.png) top right no-repeat #282828;
background-size:40%;
width: 100%;
padding: 0px 0 30px 50px;
color: #fff;
 position: relative;
 margin-top: 100px;
 box-sizing: border-box;
}
.center_box .h{
  width: 940px;
height: 15px;
margin: 0 auto;
background: #131313;
}
.token_box{
  margin-top: 35px;
}
.token_box ul{
  margin-left: 30px;
}
.token_box ul li{margin:10px 0;}
.token_box h1{
color: #949494;
padding: 30px 0;
}
.token_box h5{font-size: 15px;}
.token_box h1 span,.center_box .token_box p span,.token_box h5{color: #FF8E21;}
.center_box .token_box p{color:#fff;font-size: 1.1em;}
.center_box p {
  color: #323335;
  font-size: 1.2em;
  margin: 1.2em 0;
  line-height: 1.7em;
}
.token::before{
  content: '';
  display: inline-block;
  position: absolute;
  width: 130px;
  height: 124px;
  background: url(../../assets/img/f5/token.png) no-repeat;
background-size:100%;
    right: -90px;
    bottom: 20%;
}

.imgBox {
  background: #fff;
  padding: 2em;
  margin-top: 3em;
  box-shadow: 0px 7px 14px 0px rgba(145, 167, 255, 0.3);
}
.rowList {
  padding: 50px 25px;
  /* background: linear-gradient(180deg, #3DA2D7 0%, #6089F5 100%);
box-shadow: 0px 8px 8px 0px #A4BAEB;
border-radius: 32px; */
  /* border: 1px solid #979797; */
  /* min-height: 400px; */
  /* margin-top: 5em; */
}
.rowList img {
  margin-bottom: 10px;
}
.rowList p {
  color: #949494;
  margin-top: 40px;
}
.rowList h2 {
  font-weight: normal;
  color: #fff;
}
.introduce p {
  color: #919191;
  font-size: 1.2em;
  line-height: 1.8;
  margin-top: 2em;
}
.introduce .rowList p {
  line-height: 30px;
  margin-top: 1.8em;
  color: #fff;
}
.introduce .rowList.pink {
  box-shadow: 0px 7px 14px 0px rgba(145, 167, 255, 0.5);
  background: linear-gradient(180deg, #fff3f5 0%, #ffffff 100%);
  color: #323335;
}
.introduce .rowList.pink p,
.rowList.pink h2 {
  color: #323335;
}

.center_box {
  background: url(../../assets/img/f5/bg2.png) no-repeat #171717;
  background-size: 100%;
  padding: 1px 0;
  position: relative;
}

.hidden-sm-and-show {
  display: none;
}
@media only screen and (max-width: 450px) {
  .hidden-sm-and-show {
    display: block;
  }
  .top{
    background: url(/static/img/top_bg.bde3be5.png) top center no-repeat #171717;
    background-size: 1000px;
  }
  .head {
    padding: 10px 4%;
  }
  .head img {
    margin-top: 7px;
  }
  .head .logo{width: 80px;}
  .gou {
    padding: 5em 0;
  }
  .gou img {
    width: 60%;
  }
  .gou h1 {
    /* width: 85%; */
    font-size: 1.2em;
    padding: 0 0;
    margin: 20px 0;
  }
  .gou::after{width: 50px;}
    .gou::before{width: 90px;}
  .gou h2{
    font-weight: normal;
    font-size: 14px;
    width: 85%;
    margin: 30px auto 0;
  }
  .token{
    padding:20px;
  }
  .daoList{min-height:auto;padding: 60px 20px 30px;}
  .DAO{background-size: 78%;}
  .gou p {
    font-size: 0.8em;
    line-height: 24px;
  }
  .title {
    margin: 2em 0 0.5em;
  }
  .st {
    margin: 0 0 4em;
  }
  .col_box {
    padding: 0 5%;
    box-sizing: border-box;
  }
  .title {
    font-size: 2.2em;
  }
  .down .title {
    text-align: center;
  }
  .col_box .title::after {
    left: 50%;
    margin-left: -80px;
  }
  .down {
    min-height: 260px;
    text-align: center;
  }
  .col_box .down_btn {
    font-size: 1em;
  }
  .route {
    padding-bottom: 5em;
  }
  .foot {
    padding: 1em 5% 0;
  }
  .rowList {
    min-height: auto;
    margin-top: 2em;
    padding: 20px 25px;
  }
  .introduce p,
  .route p,
  .foot .bdive span,
  .introduce .rowList p {
    font-size: 1em;
  }
  .st p {
    font-size: 1em;
  }
  .rowList.pink h2 {
    font-size: 1.2em;
  }
  .img_box{text-align: center;}
}
@media only screen and (max-width: 767px) {
  .hidden-sm-and-show {
    display: block;
  }
  .web {
    width: 95%;
  }
}
@media (min-width: 768px) and (max-width: 992px) {
  .hidden-sm-and-show {
    display: block;
  }
}
</style>



